.mf-cert-header {
  padding-top: 36px;
  height: 151px;
  border-bottom: 1px solid #E0E0E0;
}
.mf-cert-header .mf-cert-header__wrap {
  padding-left: 70px;
}
.mf-cert-header .mf-cert-header__title {
  font-size: 20px;
  color: #333333;
  letter-spacing: 0.19px;
  font-weight: bold;
}
.mf-cert-header img {
  height: 54px;
}
.mf-cert-header .mf-cert-header__desc {
  line-height: 86px;
  font-size: 14px;
  color: #333333;
  letter-spacing: 0.14px;
  margin-left: 28px;
}
