.mf-uploader {
  width: 384px;
  display: inline-block;
}
.mf-uploader .mf-uploader__image {
  height: 120px;
}
.mf-uploader .mf-uploader__image img {
  width: 120px;
}
.mf-uploader .mf-uploader__image a {
  position: relative;
  top: 30px;
  left: 10px;
}
.mf-uploader .mf-uploader__upload {
  width: 120px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px dashed #d9d9d9;
  cursor: pointer;
}
