.mf-cert {
  flex: 1 1;
  padding: 0 40px;
  height: 100%;
  overflow: auto;
}
.mf-cert .mf-cert-content {
  padding-top: 30px;
}
.mf-cert .mf-cert-content .mf-cert-content__title {
  font-size: 20px;
  color: #333333;
  letter-spacing: 0.19px;
  margin-bottom: 30px;
  font-weight: bold;
}
.mf-cert .mf-cert-content .mf-cert-content-form__prompt {
  display: inline-block;
  width: 400px;
  vertical-align: middle;
  margin-left: 16px;
  padding: auto;
}
.mf-cert .mf-cert-content .mf-cert-content-form__prompt span {
  line-height: 24px;
  word-break: normal;
  /* width: auto; */
  display: block;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: hidden;
}
.mf-cert .mf-cert-content .mf-cert-content__reader {
  margin-left: 148px;
}
.mf-cert .mf-cert-content .mf-cert-content__commit {
  line-height: 100px;
  padding-left: 145px;
}
.mf-cert .ant-input {
  width: 384px;
}
.mf-cert .ant-form-item-control-wrapper {
  margin-left: 20px;
}
.mf-cert .mf-cert-content__type {
  width: 384px;
  font-size: 16px;
  color: #333333;
  display: inline-block;
  letter-spacing: 0.16px;
}
.mf-cert .mf-cert-content__type a {
  margin-left: 20px;
}
.mf-cert .ant-form-item label {
  white-space: normal;
}
.mf-cert .mf-cert-content-form__value__line .ant-col-2 {
  line-height: 22px;
}
.mf-button {
  background: #658FFF;
  border-radius: 4px;
  width: 120px;
  height: 40px;
}
